<template>
  <div style="margin-top: 40px">
    <div class="TestStarter">
      <el-button class="starter" @click="dialogFormVisible = true" ref="testStarter">新建</el-button>
        <el-dialog
        title="请设置您需要的测试信息"
        :visible.sync="dialogFormVisible"
        @close="clear">
          <div style="text-align:left">目标系统:
            <input type="radio" value="1" v-model="form.system.id" />
            <label for="Apollo">Apollo</label>
            <input type="radio" value="2" v-model="form.system.id" />
            <label for="Autoware">Autoware</label>
            </div>
          <div style="text-align:left">初始场景选取:
            <input type="radio" value="1" v-model="form.initialScenario.id"  @change="handleScenarioSelect(form.initialScenario.id)"/>
            <label for="从场景库选取">从场景库选取</label>
            <input type="radio" value="2" v-model="form.initialScenario.id"  @change="handleScenarioSelect(form.initialScenario.id)"/>
            <label for="本地上传">本地上传</label>
            <scenario-upload v-show="scenario_upload_visible" @onUpload="uploadScenario" ref="scenarioUpload"></scenario-upload>
            <div class="scenario-select" v-show="scenario_select_visible">
              请输入场景名称： <el-input v-model="scenario_name"></el-input>
              <el-button @click="handleScenarioSelectFromLibrary">检索场景</el-button>
            </div>
          </div>
          <div style="text-align:left">场景测试时长:
          <input v-model="form.duration" placeholder="Please enter a number" /><span>小时</span>
          </div>
          <br />
          <div style="text-align:left">场景变异对象:
          <br />
          <span>环境要素</span><br />
            <input type="checkbox" id="a100" value="100" v-model="Aenvironments" />
            <label for="天气">天气</label>
            <input type="checkbox" id="a010" value="10" v-model="Aenvironments" />
            <label for="时间">时间</label>
            <input type="checkbox" id="a001" value="1" v-model="Aenvironments" />
            <label for="道路表面">道路表面</label>
          <br />
          <span>静态障碍物</span><br />
            <span>行人:</span>
            <input type="checkbox" value="1000" v-model="Apedestrian" @change="handleShowNum(1)"/>
            <label for="增减行人数量">增减行人数量</label>
            <input type="checkbox" value="0100" v-model="Apedestrian" />
            <label for="行人类型">行人类型</label>
            <input type="checkbox" value="0010" v-model="Apedestrian" />
            <label for="行人速度">行人速度</label>
            <input type="checkbox" value="0001" v-model="Apedestrian" />
            <label for="是否允许横穿马路">是否允许横穿马路</label>
            <br />
            <div v-show="pedestrian_number_visible">
              <span>请填写行人最大数量: </span> <input type="number" v-model="form.maxPedestrian" min="0" />
              <br />
              <span>请填写行人最小数量: </span><input type="number" v-model="form.minPedestrian" min="0"  />
            </div>
            <span>路障:</span>
            <input type="checkbox" value="1" v-model="Aroadblock" @change="handleShowNum(2)"/>
            <label for="增减路障数量">增减路障数量</label>
            <br />
            <div v-show="roadblock_number_visible">
              <span>请填写路障最大数量: </span> <input type="number" v-model="form.maxRoadblock" min="0" />
              <br />
              <span>请填写路障最小数量: </span><input type="number" v-model="form.minRoadblock" min="0"  />
            </div>
          <span>动态障碍物</span><br />
            <span>机动车:</span>
            <input type="checkbox" value="100" v-model="Amotor_vehicles" @change="handleShowNum(3)"/>
            <label for="增减机动车数量">增减机动车数量</label>
            <input type="checkbox" value="010" v-model="Amotor_vehicles" />
            <label for="机动车类型及颜色">机动车类型及颜色</label>
            <input type="checkbox" value="001" v-model="Amotor_vehicles" />
            <label for="机动车速度">机动车速度</label>
            <br />
            <div v-show="motor_vehicles_number_visible">
              <span>请填写机动车最大数量: </span> <input type="number" v-model="form.maxMotorVehicles" min="0" />
              <br />
              <span>请填写机动车最小数量: </span><input type="number" v-model="form.minMotorVehicles" min="0"  />
            </div>
            <span>自行车:</span>
            <input type="checkbox" value="10" v-model="Abicycles" @change="handleShowNum(4)"/>
            <label for="增减自行车数量">增减自行车数量</label>
            <input type="checkbox" value="01" v-model="Abicycles" />
            <label for="自行车速度">自行车速度</label>
            <br />
            <div v-show="bicycles_number_visible">
              <span>请填写自行车最大数量: </span> <input type="number" v-model="form.maxBicycles" min="0" />
              <br />
              <span>请填写自行车最小数量: </span><input type="number" v-model="form.minBicycles" min="0"  />
            </div>
          <span>EGO</span><br /><span>是否包含EGO的目的地</span>
            <input type="radio" value="1" v-model="form.ego.id" />
            <label for="是">是</label>
            <input type="radio" value="2" v-model="form.ego.id" />
            <label for="否">否</label>
          <br />
          <span>交通设施</span><br /><span>是否包含交通信号灯</span>
            <input type="radio" id="d1" value="1" v-model="form.trafficFacilities.id" />
            <label for="是">是</label>
            <input type="radio" id="d2" value="2" v-model="form.trafficFacilities.id" />
            <label for="否">否</label>
          </div>
          <br />
          <div style="text-align:left">目标事故类型:
            <input type="checkbox" id="e10" value="10" v-model="Atarget_accidents" />
            <label for="碰撞">碰撞</label>
            <input type="checkbox" id="e01" value="1" v-model="Atarget_accidents" />
            <label for="驶出道路">驶出道路</label>
          </div>
          <div style="text-align:left">场景评估策略:
            <input type="checkbox" id="f100" value="100" v-model="Aevaluation_strategies" />
            <label for="场景多样性">场景多样性</label>
            <input type="checkbox" id="f010" value="10" v-model="Aevaluation_strategies" />
            <label for="与周围车辆的安全距离">与周围车辆的安全距离</label>
            <input type="checkbox" id="f001" value="1" v-model="Aevaluation_strategies" />
            <label for="规划路径的偏移">规划路径的偏移</label>
          </div>
          <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取  消</el-button>
          <el-button type="primary" @click="onSubmit">添加测试</el-button>
          </div>
        </el-dialog>
    </div>
    <div class="tests-area">
      <h1> 当前运行 </h1>
      <el-card style="text-align: left;width:98%">
        <div v-for="test in tests1" :key="test.id">
          <div style="float:left;width:85%;height: 50px;">
            <router-link class="detail-link" :to="{path:'test/testDetails', query:{id: test.id}}"><span style="font-size: 20px"><strong> ID:{{test.id}} </strong></span></router-link>
            <span> Target:{{test.system.type}} </span>
            <span> {{test.date}} </span>
            </div>
        </div>
      </el-card>

      <h1> 等待运行 </h1>
      <div>
      <el-row>
        <el-col
          v-for="test in tests0"
          :key="test.id"
          :span="8"
        >
          <el-card style="text-align: left;width:94%;height:70%">
            <div style="float:left;width:85%;height: 100px;display: flex;flex-direction: row">
              <div>
                <div><span style="font-size: 20px"><strong> ID:{{test.id}} </strong></span></div>
                <div><span> Target:{{test.system.type}} </span></div>
                <div><span> {{test.date}} </span></div>
              </div>
              <el-button style="margin-left: 40px; width: 100px; height: 50px;" @click="startTest(test.id)">启动测试</el-button>
           </div>
         </el-card>
        </el-col>
      </el-row>
      </div>

      <h1> 运行结束 </h1>
      <div>
      <el-row>
        <el-col
          v-for="test in tests2"
          :key="test.id"
          :span="8"
       >
        <el-card style="text-align: left;width:94%;height:70%">
          <div style="float:left;width:85%;height: 100px;">
             <div><router-link class="detail-link" :to="{path:'test/testResult',query:{id: test.id}}">
              <span style="font-size: 20px"><strong>ID:{{test.id}}</strong></span></router-link></div>
             <div><span> Target:{{test.system.type}} </span></div>
             <div><span> {{test.date}} </span></div>
            </div>
          </el-card>
        </el-col>
      </el-row>
      </div>
    </div>
  </div>
</template>

<script>
  import ScenarioUpload from './ScenarioUpload.vue'
  export default {
    name: 'Tests',
    components: {ScenarioUpload},
    data () {
      return {
        tests1: [],
        tests0: [],
        tests2: [],
        dialogFormVisible: false, // 控制TestStarter的显示与隐藏
        scenario_upload_visible: false, // 控制scenario-upload组件的显示和隐藏
        scenario_select_visible: false,
        pedestrian_number_visible: false, // 控制行人数量增减的显示与隐藏
        motor_vehicles_number_visible: false, // 控制机动车数量增减的显示与隐藏
        bicycles_number_visible: false, // 控制自行车数量增减的显示与隐藏
        roadblock_number_visible: false, // 控制路障数量增减的显示与隐藏
        scenario_name: '', // 场景库中场景的名字，由用户输入
        Aenvironments: [],
        Apedestrian: [],
        Amotor_vehicles: [],
        Atarget_accidents: [],
        Aevaluation_strategies: [],
        Abicycles: [],
        Aroadblock: '',
        form: {
          duration: '',
          ego: {
            id: '',
            type: ''
          },
          state: {
            id: '1',
            type: ''
          },
          system: {
            id: '',
            type: ''
          },
          initialScenario: {
            id: '',
            type: ''
          },
          initial_scenario_path: '', // 初始场景路径，如果是用户上传的，那么为场景文件在服务器中的路径；如果是用户从场景库中选取的，那么为场景的id，根据场景id可以找到场景路径（注意需要把id从String转为int）
          environments: {
            id: '',
            type: ''
          },
          pedestrian: {
            id: '',
            type: ''
          },
          roadblock: {
            id: '',
            type: ''
          },
          motorVehicles: {
            id: '',
            type: ''
          },
          bicycles: {
            id: '',
            type: ''
          },
          trafficFacilities: {
            id: '',
            type: ''
          },
          targetAccidents: {
            id: '',
            type: ''
          },
          evaluationStrategies: {
            id: '',
            type: ''
          },
          maxPedestrian: 0,
          minPedestrian: 0,
          maxRoadblock: 0,
          minRoadblock: 0,
          maxMotorVehicles: 0,
          minMotorVehicles: 0,
          maxBicycles: 0,
          minBicycles: 0
        }
      }
    },
    mounted () {
      this.loadTestcases1()
      this.loadTestcases0()
      this.loadTestcases2()
    },
    methods: {
      loadTestcases1 () {
        var _this = this
        this.$axios.get('/testcases/state' + '/2').then(resp => {
          if (resp && resp.data.code === 200) {
            _this.tests1 = resp.data.result
          }
        })
      },
      loadTestcases0 () {
        var _this = this
        this.$axios.get('/testcases/state' + '/1').then(resp => {
          if (resp && resp.data.code === 200) {
            _this.tests0 = resp.data.result
          }
        })
      },
      loadTestcases2 () {
        var _this = this
        this.$axios.get('/testcases/state' + '/3').then(resp => {
          if (resp && resp.data.code === 200) {
            _this.tests2 = resp.data.result
          }
        })
      },
      handleShowNum (type) {
        if (type === 1) {
          this.pedestrian_number_visible = true
        } else if (type === 3) {
          this.motor_vehicles_number_visible = true
        } else if (type === 4) {
          this.bicycles_number_visible = true
        } else if (type === 2) {
          this.roadblock_number_visible = true
        }
      },
      handleScenarioSelect (value) {
        // eslint-disable-next-line eqeqeq
        if (value == 1) {
          this.scenario_select_visible = true
          this.scenario_upload_visible = false
        }
        // eslint-disable-next-line eqeqeq
        if (value == 2) {
          this.scenario_select_visible = false
          this.scenario_upload_visible = true
        }
      },
      handleScenarioSelectFromLibrary () {
        var _this = this
        if (this.scenario_name === '') {
          this.$alert('请输入场景库中具体的场景名称', '提示', {
            confirmButtonText: '确定'
          })
        } else {
          this.$axios.get('/scenarios/searchbyname/' + this.scenario_name).then(resp => {
            if (resp && resp.data.code === 200) {
              _this.form.initial_scenario_path = resp.data.result
              // console.log(_this.form.initial_scenario_path)
              // console.log(resp.data.result)
              this.$alert('已找到该场景，请继续选择其他测试信息', '提示', {
                confirmButtonText: '确定'
              })
            } else {
              _this.form.initial_scenario_path = ''
              this.$alert(resp.data.message, '提示', {
                confirmButtonText: '确定'
              })
            }
          })
        }
      },
      uploadScenario () {
        this.form.initial_scenario_path = this.$refs.scenarioUpload.filePath
      },
      clear () {
        this.$refs.scenarioUpload.handleRemove()
        this.scenario_select_visible = false
        this.scenario_upload_visible = false
        this.pedestrian_number_visible = false
        this.motor_vehicles_number_visible = false
        this.bicycles_number_visible = false
        this.roadblock_number_visible = false
        this.Aenvironments = []
        this.Apedestrian = []
        this.Amotor_vehicles = []
        this.Abicycles = []
        this.Atarget_accidents = []
        this.Aevaluation_strategies = []
        this.Aroadblock = ''
        this.scenario_name = ''
        this.form = {
          duration: '',
          ego: {
            id: '',
            type: ''
          },
          system: {
            id: '',
            type: ''
          },
          initialScenario: {
            id: '',
            type: ''
          },
          initial_scenario_path: '',
          environments: {
            id: '',
            type: ''
          },
          pedestrian: {
            id: '',
            type: ''
          },
          roadblock: {
            id: '',
            type: ''
          },
          motorVehicles: {
            id: '',
            type: ''
          },
          bicycles: {
            id: '',
            type: ''
          },
          trafficFacilities: {
            id: '',
            type: ''
          },
          targetAccidents: {
            id: '',
            type: ''
          },
          evaluationStrategies: {
            id: '',
            type: ''
          },
          maxPedestrian: 0,
          minPedestrian: 0,
          maxRoadblock: 0,
          minRoadblock: 0,
          maxMotorVehicles: 0,
          minMotorVehicles: 0,
          maxBicycles: 0,
          minBicycles: 0
        }
      },
      onSubmit () {
        if (this.form.initial_scenario_path === '') {
          this.$alert('请选择初始场景！如果您选择场景库中的场景，您可能没有进行检索！如果您选择从本地上传场景，您可能没有点击上传按钮！', '提示', {
            confirmButtonText: '确定'
          })
        } else {
          var date = new Date()
          var date1 = date.toLocaleDateString()
          var s1 = 0
          var s2 = 0
          var s3 = 0
          var s4 = 0
          var s5 = 0
          var s6 = 0
          var s7 = 0
          for (var i = 0; i < this.Aenvironments.length; i++) {
            s1 += Number(this.Aenvironments[i])
          }
          for (var j = 0; j < this.Apedestrian.length; j++) {
            s2 += Number(this.Apedestrian[j])
          }
          for (var k = 0; k < this.Amotor_vehicles.length; k++) {
            s3 += Number(this.Amotor_vehicles[k])
          }
          for (var m = 0; m < this.Abicycles.length; m++) {
            s6 += Number(this.Abicycles[m])
          }
          for (var n = 0; n < this.Atarget_accidents.length; n++) {
            s4 += Number(this.Atarget_accidents[n])
          }
          for (var g = 0; g < this.Aevaluation_strategies.length; g++) {
            s5 += Number(this.Aevaluation_strategies[g])
          }
          s7 = Number(this.Aroadblock)
          this.form.environments.id = s1
          this.form.pedestrian.id = s2
          this.form.motorVehicles.id = s3
          this.form.targetAccidents.id = s4
          this.form.evaluationStrategies.id = s5
          this.form.bicycles.id = s6
          this.form.roadblock.id = s7
          this.$axios
            .post('/edit/testcases', {
              date: date1,
              duration: this.form.duration,
              ego: this.form.ego,
              state: this.form.state,
              system: this.form.system,
              initialScenario: this.form.initialScenario,
              initialScenarioPath: this.form.initial_scenario_path,
              environments: this.form.environments,
              pedestrian: this.form.pedestrian,
              roadblock: this.form.roadblock,
              motorVehicles: this.form.motorVehicles,
              bicycles: this.form.bicycles,
              trafficFacilities: this.form.trafficFacilities,
              targetAccidents: this.form.targetAccidents,
              evaluationStrategies: this.form.evaluationStrategies,
              maxPedestrian: this.form.maxPedestrian,
              minPedestrian: this.form.minPedestrian,
              maxRoadblock: this.form.maxRoadblock,
              minRoadblock: this.form.minRoadblock,
              maxMotorVehicles: this.form.maxMotorVehicles,
              minMotorVehicles: this.form.minMotorVehicles,
              maxBicycles: this.form.maxBicycles,
              minBicycles: this.form.minBicycles
            }).then(resp => {
            if (resp && resp.data.code === 200) {
              this.dialogFormVisible = false
              this.$router.go(0)
            }
          })
        }
      },
      startTest (testCaseId) {
        console.log('调用脚本中...')
        this.$axios.post('/callscript/starttest', {id: testCaseId})
          .then(resp => {
            if (resp.data.code === 200) {
              this.$alert(resp.data.message, '提示', {
                confirmButtonText: '确定调用成功'
              })
              this.$router.go(0)
            } else {
              this.$alert(resp.data.message, '提示', {
                confirmButtonText: '确定调用失败'
              })
            }
          })
          .catch(function (error) {
            console.log(error)
          })
      }
    }
  }
</script>

<style scoped>
  .tests-area {
    width: 990px;
    height: 750px;
    margin-left: auto;
    margin-right: auto;
  }

  .detail-link {
    text-decoration: none;
    color: #606266;
  }

  .detail-link:hover {
    color: #409EFF;
  }
</style>



